<template>
  <div class="content-data">
    <el-card class="search-box">
      <el-form
        :model="formData"
        label-width="80px"
        :inline="true"
      >
        <el-form-item
          prop="date"
          label="日期"
        >
          <el-date-picker
            v-model="formData.date"
            type="date"
            value-format="yyyy-MM-dd"
            size="small"
            placeholder="选择日期"
            :picker-options="pickerOptions"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button
            :loading="btnLoading"
            size="small"
            type="primary"
            @click="search"
          >查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button
            size="small"
            @click="upload"
            :disabled="tableData.length === 0"
          >导出</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card>
      <el-table
        v-loading="tableLoading"
        :data="tableData"
      >
        <el-table-column
          :fixed="true"
          prop="schoolName"
          label="分校"
        >
        </el-table-column>
        <el-table-column
          :fixed="true"
          prop="serviceDeptName"
          width="180"
          label="服务中心"
        >
        </el-table-column>
        <el-table-column
          :fixed="true"
          prop="notLoginTimeSum"
          width="130"
          label="累计未排班时长(单位：分钟)"
        >
        </el-table-column>
        <el-table-column
          v-for="item in Object.keys(timeTfield)"
          :prop="timeTfield[item]"
          align="center"
          :label="item"
          width="105"
          :key="item"
        >
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { serviceNotShift } from '../../api/advisor'
import { mapState } from 'vuex'
import moment from 'moment'
import util from '@/common/util.js'
import config from '../../config'
import { runEnv } from '../../libs/util'
const env = runEnv()
const urls = {
  exportUrl: config.baseUrl[env] + '/SPLAT/crmPanelList/serviceNotShiftDownload'
}
export default {
  name: 'shiftWatch',
  data () {
    return {
      timeTfield: {
        '9:00-10:00': 'nine',
        '10:00-11:00': 'ten',
        '11:00-12:00': 'eleven',
        '12:00-13:00': 'twelve',
        '13:00-14:00': 'thirteen',
        '14:00-15:00': 'fourteen',
        '15:00-16:00': 'fifteen',
        '16:00-17:00': 'sixteen',
        '17:00-18:00': 'seventeen',
        '18:00-19:00': 'eighteen',
        '19:00-20:00': 'nineteen',
        '20:00-21:00': 'twenty'
      },
      tableData: [],
      btnLoading: false,
      tableLoading: false,
      loading: false,
      formData: {
        date: ''
      },
      searchParams: {},
      pickerOptions: {
        disabledDate: (time) => {
          return this.dealDisabledDate(time)
        }
      }
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo'])
  },
  mounted () {
    let yesterday = moment(new Date()).add(-1, 'days').format('yyyy-MM-DD')
    this.formData.date = yesterday
    this.searchParams = {
      date: this.formData.date,
      areaCode: this.userInfo.cityCode
    }
    this.queryTableData(this.searchParams)
  },
  methods: {
    async queryTableData (params) {
      this.tableData = []
      this.btnLoading = true
      this.tableLoading = true
      try {
        this.tableData = await serviceNotShift(params)
        this.btnLoading = false
        this.tableLoading = false
      } catch (err) {
        this.$message.error(err.message)
        this.btnLoading = false
        this.tableLoading = false
      }
    },
    search () {
      this.btnLoading = true
      let params = {
        ...this.searchParams,
        ...this.formData
      }
      this.queryTableData(params)
    },
    upload () {
      let params = {
        ...this.searchParams,
        ...this.formData
      }
      util.download(urls.exportUrl, params, 'get')
    },
    dealDisabledDate (time) {
      var times = Date.now() - 8.64e7
      return time.getTime() >= times
    }
  }
}
</script>

<style scoped lang="less">
.content-data {
  width: 100%;
  .search-box {
    margin-bottom: 15px;
  }
}
</style>
