<template>
  <Layout id="onlineAdviserSchedule" class="gary">
    <Form v-if="this.serviceCenterList.length">
      <FormItem label="服务中心筛选：">
        <Select v-model="serviceCenterId" style="width: 300px" @on-change="serviceCenterChange" filterable>
          <Option v-for="item in serviceCenterList" :key="item.id" :value="item.id">{{ item.deptName }}</Option>
        </Select>
      </FormItem>
    </Form>
    <Radio-group
      v-model="weekDay"
      type="button"
      size="large"
      @on-change="getDataByDate"
    >
      <Radio
        class="radio_margin"
        v-for="item in weekDayList"
        :key="item.date"
        :label="item.date"
      >
        {{ `${numTransWeek[item.week]} (${item.date.slice(5)})` }}
      </Radio>
    </Radio-group>
    <div class="notice">
      <p>【注意事项】</p>
      <p>
        1.编辑完成一天的排班后请务必保存后再继续编辑；连续编辑多天排班后再点击保存时，系统只会保存最后一天的排班；
      </p>
      <p>2.点击保存后，排班更改实时生效，请确认编辑无误后再保存；</p>
      <p>3.建议每次编辑后，将排班表整体核实一遍，以免编辑错误影响工作；</p>
      <p>
        4.排班列表右上角黄色角标代表该顾问在该时段已委托给他人代维护，详细代班关系查询请移步至“代班管理”页面查看；
      </p>
    </div>
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size="18" class="demo-spin-icon-load"></Icon>
    </Spin>
    <table
      v-else
      border="1"
      cellspacing="0"
      cellpadding="0"
      bgcolor="#ffffff"
      bordercolordark="#ffffff"
      bordercolorlight="#000000"
      style="
        table-layout: fixed;
        border: 1px solid #ddd;
        border-collapse: collapse;
        margin-top: 10px;
      "
    >
      <thead>
        <tr style="">
          <th style="height: 50px">&nbsp;</th>
          <th
            v-for="adv in onlineAdvisers"
            :key="adv.identityId"
            style="
              font-size: 16px;
              font-weight: bold;
              color: #444;
              width: 20px;
              font-family: 'Microsoft YaHei';
              word-break: break-all;
            "
          >
            {{ adv.userName }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="int in intervalList" :key="int.workHour">
          <td
            style="
              text-align: center;
              width: 100px;
              font-size: 16px;
              font-weight: bold;
              color: #333;
              min-width: 100px;
              white-space: nowrap;
            "
          >
            {{ int.workStartTime + '-' + int.workEndTime }}
          </td>
          <td
            v-for="adv in onlineAdvisers"
            :key="adv.identityId"
            @click="changeSchedule(int, adv)"
            :class="displayOnOff(int, adv) ? 'schedule-on' : 'schedule-off'"
          >
            <div v-if="isAuthorize(int, adv)" class="icon">代</div>
          </td>
        </tr>
      </tbody>
    </table>
    <div style="margin-top: 10px; text-align: right">
      <Button v-show="currentIsEdit && userInfo.crmRoleId !== 2" type="primary" @click="save">保存</Button>
    </div>
  </Layout>
</template>

<script>
import {
  getOnlineShiftByDate,
  insertOnlineAdvisorShift,
  getOnlineConsults,
  getSchoolConfiguration,
  getWeekDay
} from '@/api/advisor'
import { getDeptByAreaCode } from '@/api/crm'

import moment from 'moment'
export default {
  name: 'consultshift',
  data () {
    return {
      serviceCenterId: '',
      serviceCenterList: '',
      weekDay: '',
      tableData: [],
      intervalList: [],
      scheduleList: [],
      onlineAdvisers: [],
      dataList: {},
      loading: false,
      numTransWeek: {
        1: '周一',
        2: '周二',
        3: '周三',
        4: '周四',
        5: '周五',
        6: '周六',
        7: '周日'
      },
      todayTime: '',
      currentIsEdit: false,
      weekDayList: []
    }
  },
  methods: {
    async serviceCenterChange () {
      try {
        this.onlineAdvisers = await getOnlineConsults({
          cityCode: this.userInfo.cityCode,
          serviceId: this.serviceCenterId
        })
        this.getDataByDate()
      } catch (e) {
        this.$Message.error({
          content: '初始化失败：' + e.message || e,
          duration: 5
        })
      }
    },
    /**
     * 按日期查询排班数据
     */
    getDataByDate () {
      this.currentIsEdit = this.weekDayList.findIndex(item => item.date === this.weekDay) !== -1 ? this.weekDayList[this.weekDayList.findIndex(item => item.date === this.weekDay)].editable : false
      this.loading = true
      getOnlineShiftByDate({
        cityCode: this.userInfo.cityCode,
        serviceId: this.serviceCenterId,
        date: this.weekDay
      }).then(data => {
        this.scheduleList = data
      }).catch(err => {
        this.$Message.error({
          content: '查询排班信息错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.loading = false
      })
    },
    /**
     * 改变排班
     * @param int 排班时间
     * @param adv 排班人
     */
    changeSchedule (int, adv) {
      if (this.userInfo.crmRoleId === 2) {
        this.$Message.warning({
          content: '城市经理不能设置排班',
          duration: 5
        })
        return
      }
      const schedule = this.scheduleList.find(s => s.workHour === int.workHour && s.onlineIdentityId === String(adv.identityId))
      if (schedule) {
        schedule.deleted = schedule.deleted === 0 ? 1 : 0
      } else {
        this.scheduleList.push({
          workStartTime: int.workStartTime,
          workEndTime: int.workEndTime,
          deleted: 0,
          onlineIdentityId: String(adv.identityId),
          workHour: int.workHour,
          userWorkCode: adv.userWorkCode
        })
      }
    },
    save () {
      this.$Modal.confirm({
        title: '保存排班',
        content: '<p>1.编辑完成一天的排班后请务必保存后再继续编辑；连续编辑多天排班后再点击保存时，系统只会保存最后一天的排班；</p><p>2.排班更改将在点击"确定"后实时生效，是否仍要更改？</p>',
        // loading: true,
        onOk: () => {
          insertOnlineAdvisorShift({
            details: this.scheduleList,
            localDate: this.weekDay
          }).then(data => {
            this.$Message.success({
              content: '保存成功',
              duration: 5
            })
            this.$Modal.remove()
            this.getDataByDate()
          }).catch(err => {
            this.$Message.error({
              content: '保存失败：' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    /**
     * 是否代班
     */
    isAuthorize (int, adv) {
      const schedule = this.scheduleList.find(s => s.workHour === int.workHour && s.onlineIdentityId === String(adv.identityId) && s.isAuthorize)
      if (schedule) {
        return schedule.deleted === 0
      } else {
        return false
      }
    },
    displayOnOff (int, adv) {
      const schedule = this.scheduleList.find(s => s.workHour === int.workHour && s.onlineIdentityId === String(adv.identityId))
      if (schedule) {
        return schedule.deleted === 0
      } else {
        return false
      }
    },
    /**
     * 查询星期列表
     */
    queryWeekDay () {
      getWeekDay().then(data => {
        this.weekDayList = data
      }).catch(e => {
        this.$Message.error({
          content: '初始化失败：' + e.message || e,
          duration: 5
        })
      })
    }
  },
  computed: {
    userInfo () {
      return this.$store.state.roleRelativeData.userInfo
    }
  },
  async mounted () {
    try {
      this.loading = true
      this.todayTime = moment(new Date()).format('YYYY-MM-DD')
      this.weekDay = this.todayTime
      this.queryWeekDay()
      if (this.userInfo.crmRoleId === 2) { // 如果是城市经理，查询管理的服务中心
        this.serviceCenterList = await getDeptByAreaCode({
          areaCode: this.userInfo.cityCode,
          isActive: 1
        })
        this.serviceCenterId = this.serviceCenterList[0].id
      } else {
        this.serviceCenterId = this.userInfo.crmDeptId
      }
      this.onlineAdvisers = await getOnlineConsults({
        cityCode: this.userInfo.cityCode,
        serviceId: this.serviceCenterId
      })
      const schoolConfig = await getSchoolConfiguration()
      for (let i = schoolConfig.startHour; i <= schoolConfig.endHour; i++) {
        this.intervalList.push({
          workHour: i,
          workStartTime: `${i}:00`,
          workEndTime: `${i + 1}:00`
        })
      }
      this.getDataByDate()
    } catch (e) {
      this.$Message.error({
        content: '初始化失败：' + e.message || e,
        duration: 5
      })
    } finally {
      this.loading = false
    }
  }
}
</script>

<style scoped lang="less">
#onlineAdviserSchedule {
  /*width: 100%;*/
  overflow-x: auto;
  padding: 10px;
  position: relative;
}

.notice {
  margin-top: 10px;
}

.schedule-off {
  padding-top: 5px;
  height: 40px;
  background-color: #e9f3ff;
  cursor: pointer;
  position: relative;
  :hover {
    background-color: #92a6bb;
  }
}

.schedule-on {
  padding-top: 5px;
  height: 40px;
  background-color: #81bdff;
  cursor: pointer;
  position: relative;
  z-index: 20;
  animation: scale 0.2s;
  :hover {
    background-color: #92a6bb;
  }
}
@keyframes scale {
  30% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes scale /*Safari and Chrome*/ {
  30% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

.icon {
  width: 16px;
  height: 20px;
  padding: 2px;
  font-size: 12px;
  position: absolute;
  top: 0;
  right: 0;
  border-bottom-left-radius: 5px;
  background: #ffca43;
}

.demo-spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}

@keyframes ani-demo-spin {
  from {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(180deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.radio_margin {
  margin: 0 5px 5px 0;
}
</style>
