<template>
  <Layout id="entrust-wrapper" class="gary">
    <div class="header">
      <RadioGroup v-model="weekDay" type="button" size="large" @on-change="getDataByDate">
        <Radio label="1">周一</Radio>
        <Radio label="2">周二</Radio>
        <Radio label="3">周三</Radio>
        <Radio label="4">周四</Radio>
        <Radio label="5">周五</Radio>
        <Radio label="6">周六</Radio>
        <Radio label="7">周日</Radio>
      </RadioGroup>
      <div v-if="$haveAuthority('roleRelativeData', '/shift/entrustshift/edit')">
        <Button class="add-btn" type="primary" icon="md-add" @click="openAdd">新增代班设置</Button>
        <Button icon="md-trash" :disabled="!ids.length" @click="deleteAgent">删除代班设置</Button>
      </div>
    </div>
    <div class="content">
      <div class="table-wrapper">
        <Spin fix v-if="loading">
          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        </Spin>
        <Table border :columns="tableColumns" :data="tableData" @on-selection-change="selectionChange">
          <template slot-scope="{ row }" slot="workHour">
            {{ row.workHour + ':00-' + (row.workHour + 1) + ':00'}}
          </template>
          <template slot-scope="{ row }" slot="agents">
            {{ row.agents.map(a => a.assignorWorkName).join(',') }}
          </template>
        </Table>
      </div>
      <div class="notice">
        <p class="title">【代班功能说明】</p>
        <p> 1.代班功能主要解决顾问伙伴在其他伙伴休假倒班时需用盘古登录服务中心他人账号进行IM维护的问题；通过代班机制可以实现在代班期间代班顾问仅需登录自己的账号即可同时维护原顾问的IM中用户发来的全部文字及语音消息。</p>
        <p> 2.代班期间所产生的全部文字，语音的消息响应时长均不计入代办顾问与原顾问的响应时长中。</p>
        <p> 3.在代班期间内，原顾问的盘古端将接收不到用户发来的消息，均转移至代办顾问账号下；但手机端云客CRM则不受影响。</p>
        <p> 4.在代班期间内，代班顾问和家长没有建立沟通的情况下，原顾问仍可通过自己的盘古系统发送消息给用户。</p>
        <p class="title">【代班设置规则】</p>
        <p> 1.代班功能的代班顾问与原顾问均为同服务中心顾问角色。</p>
        <p> 2.一个顾问在同一时段内不能既为代班顾问又为原顾问。</p>
        <p> 3.一个顾问作为原顾问在同一时段内不能委托给两个顾问代班。</p>
      </div>
    </div>
    <Modal v-model="addModal" title="新增代班设置" @cancel="cancel" footer-hide :closable="false" :mask-closable="false">
      <Form ref="form" :label-width="80" :rules="ruleForm" :model="formData">
        <FormItem label="代班日期">
          {{ dayName }}
        </FormItem>
        <FormItem label="代班顾问" prop="agentIdentityId">
          <Select v-model="formData.agentIdentityId" clearable filterable @on-clear="formData.agentIds = []">
            <Option v-for="(item,index) in advisers" :value="item.identityId" :key="index">{{ item.userName }}</Option>
          </Select>
        </FormItem>
        <FormItem label="原顾问" prop="agentIds">
          <Select v-model="formData.agentIds" multiple clearable filterable :disabled="!formData.agentIdentityId">
            <Option v-for="(item,index) in advisers" :disabled="formData.agentIdentityId === item.identityId" :value="item.identityId" :key="index">{{ item.userName }}</Option>
          </Select>
        </FormItem>
        <FormItem label="代班时段" prop="workHours">
          <Select v-model="formData.workHours" multiple clearable :disabled="!formData.agentIdentityId || !formData.agentIds.length">
            <Option v-for="(item,index) in intervalList" :value="item.workHour" :key="index">{{ item.workStartTime + '-' + item.workEndTime }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div style="text-align: right">
        <Button @click="cancel">取消</Button>
        <Button @click="save" type="primary">确定</Button>
      </div>
    </Modal>
  </Layout>
</template>

<script>
import {
  getAdvisorAuthorizeByDate,
  getSchoolConfiguration,
  getOnlineConsults,
  insertAdvisorAuthorize,
  deleteAdvisorAuthorize
} from '@/api/advisor'
export default {
  name: 'entrustshift',
  data () {
    return {
      weekDay: '1',
      tableData: [],
      intervalList: [],
      scheduleList: [],
      advisers: [],
      formData: {
        agentIdentityId: '', // 代班顾问身份id
        agentIds: [], // 原顾问身份id
        workHours: [] // 代班时段
      },
      loading: false,
      tableColumns: [
        {
          type: 'selection',
          width: 50,
          align: 'center'
        },
        {
          title: '委托时段',
          slot: 'workHour',
          width: 150,
          align: 'center'
        },
        {
          title: '代班顾问',
          key: 'agentWorkName',
          align: 'center'
        },
        {
          title: '原顾问',
          slot: 'agents',
          align: 'center'
        }
      ],
      ruleForm: {
        agentIdentityId: [{ required: true, message: '代班顾问不能为空', trigger: 'change', type: 'number' }],
        agentIds: [{ required: true, message: '原顾问不能为空', trigger: 'change', type: 'array' }],
        workHours: [{ required: true, message: '代班时段不能为空', trigger: 'change', type: 'array' }]
      },
      addModal: false,
      ids: []
    }
  },
  methods: {
    getDataByDate (weekDay) {
      this.ids = []
      this.loading = true
      getAdvisorAuthorizeByDate({
        cityCode: '010',
        weekDay
      }).then(data => {
        this.tableData = data
      }).catch(err => {
        this.$Message.error({
          content: '查询顾问委托列表错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.loading = false
      })
    },
    openAdd () {
      this.addModal = true
    },
    save () {
      this.$refs.form.validate(valid => {
        if (valid) {
          // 组装原顾问信息
          const agents = this.advisers.filter(a => this.formData.agentIds.includes(a.identityId)).map(a => {
            return {
              assignorIdentityId: String(a.identityId),
              assignorWorkCode: a.userWorkCode,
              assignorWorkName: a.userName
            }
          })
          const adv = this.advisers.find(a => a.identityId === this.formData.agentIdentityId)
          const params = Object.assign({}, this.formData, {
            agentIdentityId: String(adv.identityId),
            agentWorkCode: adv.userWorkCode,
            agentWorkName: adv.userName,
            agents,
            weekDay: this.weekDay
          })
          insertAdvisorAuthorize(params).then(data => {
            this.$Message.success({
              content: '保存成功',
              duration: 5
            })
            this.cancel()
            this.getDataByDate(this.weekDay)
          }).catch(err => {
            this.$Modal.error({
              title: '保存失败',
              render: (h) => {
                return h('div', [
                  h('p', '请修改后重试！'),
                  h('p', '1.代班顾问在选中的时段中不得同时存在“原顾问”身份；'),
                  h('p', '2.原顾问在选中的时段内不得同时被两个人代班；')
                ])
              },
              content: '保存失败：' + err.message
            })
          })
        } else {
          this.$Message.warning({
            content: '请完善必填信息',
            duration: 5
          })
        }
      })
    },
    cancel () {
      this.addModal = false
      this.formData = {
        agentIdentityId: '', // 代班顾问身份id
        agentIds: [], // 原顾问身份id
        workHours: [] // 代班时段
      }
      this.$refs.form.resetFields()
    },
    selectionChange (rows) {
      this.ids = rows.map(r => r.ids)
    },
    deleteAgent () {
      this.$Modal.confirm({
        title: '删除确认',
        content: `选中的代班设置执行“删除”操作在点击“确认”后将实时生效，是否仍要更改？`,
        onOk: () => {
          deleteAdvisorAuthorize(this.ids).then(() => {
            this.$Message.success('删除成功！')
            this.getDataByDate(this.weekDay)
          }).catch(err => {
            this.$Message.error('删除失败：' + err.message)
          })
        }
      })
    }
  },
  computed: {
    dayName () {
      switch (this.weekDay) {
        case '1':
          return '周一'
        case '2':
          return '周二'
        case '3':
          return '周三'
        case '4':
          return '周四'
        case '5':
          return '周五'
        case '6':
          return '周六'
        case '7':
          return '周日'
        default:
          return ''
      }
    }
  },
  async mounted () {
    try {
      this.loading = true
      this.advisers = await getOnlineConsults()
      const schoolConfig = await getSchoolConfiguration({
        // cityCode: '010'
      })
      for (let i = schoolConfig.startHour; i <= schoolConfig.endHour; i++) {
        this.intervalList.push({
          workHour: i,
          workStartTime: `${i}:00`,
          workEndTime: `${i + 1}:00`
        })
      }
      this.getDataByDate(1)
    } catch (e) {
      this.$Message.error({
        content: '初始化失败：' + e.message || e,
        duration: 5
      })
      this.loading = false
    }
  }
}
</script>

<style scoped lang="less">
  #entrust-wrapper {
    /*width: 100%;*/
    overflow-x: auto;
    padding: 10px;
    position: relative;
    .header {
      display: flex;
      margin-bottom: 10px;
      .add-btn {
        margin-left: 20px;
        margin-right: 10px;
      }
    }
    .content {
      display: flex;
      .table-wrapper {
        width: 70%;
        margin-right: 20px;
        .demo-spin-icon-load{
          animation: ani-demo-spin 1s linear infinite;
        }
        @keyframes ani-demo-spin {
          from { transform: rotate(0deg);}
          50%  { transform: rotate(180deg);}
          to   { transform: rotate(360deg);}
        }
      }
      .notice {
        margin-top: 10px;
        p {
          line-height: 20px;
          margin-bottom: 10px;
        }
        .title {
          font-weight: bold;
        }
      }
    }
  }

</style>
